import React, { useState } from "react";
import { Eye, EyeClosed, Grip } from "lucide-react";

interface CommonInfoDropdownHeaderProps {
  title: string;
  executiveFunction?: () => void;
}

function CommonInfoDropdownHeader(props: CommonInfoDropdownHeaderProps) {
  const { title, executiveFunction } = props;

  const [visible, setVisible] = useState(true);

  return (
    <div className="-m-2common-info-dropdown-header__component d-flex align-items-center justify-content-between">
      <div className="triger-button">
        <Grip />
      </div>

      <div
        role="button"
        tabIndex={0}
        className="title"
        onClick={executiveFunction}
      >
        {title}
      </div>
      <div
        role="button"
        tabIndex={0}
        className="icon-wrapper"
        onClick={() => setVisible(!visible)}
      >
        {visible ? <Eye /> : <EyeClosed />}
      </div>
    </div>
  );
}

export default CommonInfoDropdownHeader;
